*,*::after, *::before {
	@include borderbox; }

.main {
	width: 100%;
	height: 118px;
	background: $black;
	margin-bottom: 100px;
	position: relative;
	@include fortablet {
		height: auto;
		margin-bottom: 30px; }
	@include formobile {
		height: auto;
		margin-bottom: 0; }


	&.fixed {
		position: fixed;
		left: 0;
		top: 0;
		z-index: 10;
		height: 50px;
		@include formobile {
			height: auto; }

		.topbar {
			display: none; }
		.site-title {
			height: 50px;
			line-height: 55px; }
		.social {
			height: 50px;
			line-height: 50px;
			.searchbox {
				margin: 10px 0; } }
		.burgermenu {
			@include fortablet {
				margin: 15px 0; } }
		.main-nav {
			height: 50px;
			line-height: 50px;
			@include fortablet {
				height: auto; }
			@include formobile {
				height: auto; }
			.nav {
				padding: 0;
				a {
					padding-bottom: 0;
					&:after {
						display: none; } } } } }

	.topbar {
		width: 100%;
		height: 28px;
		background: $red-light-1;

		@include formobile {
			overflow: hidden;
			height: auto;
			max-height: 28px;
			@include transitionspeed(50ms);

			&.slideDown {
				max-height: 1000px;
				@include transitionspeed(1s); } } } }




