.section-title {
	font-family: $opensans;
	font-size: 34px;
	line-height: 150px;
	display: block;
	font-weight: 700;
	text-align: center;

	span {
		color: $red; } }

.sectionlist {
	width: 100%;
	height: auto;
	display: block;
	margin: 0 0 30px 0;
	padding: 0;
	overflow: hidden;

	@include formobile {
		@include calcu(width,'100% + 20px');
		margin-left: -10px; }



	li {
		float: left;
		width: 100%;
		@include calcu(width,'25% - 45px');
		position: relative;
		margin-right: 30px;

		@include fortablet {
			@include calcu(width,'100% / 4 - 10px');
			@include calcu(margin-right,'10px * 4 / 3'); }
		@include formobile {
			@include calcu(width,'100% / 2 - 20px');
			margin: 0 10px 0; }

		&:after {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			@include transitionspeed(2s); }

		h2 {
			position: absolute;
			bottom: 0;
			left: 0;
			color: $white;
			font-family: $opensans;
			font-size: 20px;
			font-weight: 600;
			display: block;
			padding: 20px;
			z-index: 1;
			@include fortablet {
				font-size: 14px;
				padding: 10px; }
			@include formobile {
				font-size: 14px;
				padding: 10px; }

			a {
				@include transitionspeed(2s);
				color: $white; } }

		img {
			display: block;
			width: 100%;
			@include transitionspeed(2s);
			@include transform(scale(1)); }

		.small-thumbnail {
			width: 100%;
			height: auto;
			overflow: hidden;
			position: relative; }


		&.featured-section {
			@include calcu(width,'50% + 25px');
			max-width: none;
			@include fortablet {
				width: 100%;
				margin-right: 0;
				margin-bottom: 30px;
				margin-left: 0; }
			@include formobile {
				width: 100%;
				margin-bottom: 20px;
				margin-left: 0; }



			h2 {
				font-size: 32px;
				padding: 20px;
				font-weight: 700;
				z-index: 1;
				@include fortablet {
					font-size: 50px; }
				@include formobile {
					font-size: 24px;
					padding: 10px; } }

			.thumbnail {
				width: 100%;
				height: auto;
				overflow: hidden;
				position: relative; } }

		&:last-child,&:nth-child(3) {
			margin-right: 0;
			@include fortablet {
				margin-right: 10px; } }

		&:nth-child(2),&:nth-child(3) {
			margin-bottom: 35px;
			@include formobile {
				margin-bottom: 20px; } }

		&:last-child {
			@include fortablet {
				margin-right: 0; } }

		&:hover {
			&:after {
				@include transitionspeed(2s);
				@include boxshadow($black 0 -80px 100px inset); }
			h2 {
				a {
					@include transitionspeed(2s);
					@include textshadow($black 0 2px 2px); } }
			img {
				@include transitionspeed(2s);
				@include imgfilter(contrast(1));
				@include transform(scale(1.1)); } } } }

